import { connect } from 'react-redux';
import { Redirect, Route, Switch } from 'react-router-dom';
import PropTypes from 'prop-types';
import Menu from '../menu';
import Reviews from '../reviews';
import Banner from '../banner';
import Rate from '../rate';
import Tabs from '../tabs';
import {
  averageRatingSelector,
  restaurantSelector,
} from '../../redux/selectors';

const env = process.env.NODE_ENV;
const domain = env === 'development' ? '' : '/react-2021-10-08';

const Restaurant = ({ restaurant, averageRating }) => {
  const { id, name, menu, reviews } = restaurant;

  const menuUrl = `${domain}/restaurants/${id}/menu`;
  const reviewsUrl = `${domain}/restaurants/${id}/reviews`;

  const menuPath = `${domain}/restaurants/:restId/menu`;
  const reviewsPath = `${domain}/restaurants/:restId/reviews`;

  const tabs = [
    { label: 'Menu', url: menuUrl },
    { label: 'Reviews', url: reviewsUrl },
  ];

  return (
    <div>
      <Banner heading={name}>
        <Rate value={averageRating} />
      </Banner>
      <Tabs tabs={tabs} />
      <Switch>
        <Route path={menuPath}>
          <Menu menu={menu} restId={id} />
        </Route>
        <Route path={reviewsPath}>
          <Reviews reviews={reviews} restId={id} />
        </Route>
        <Redirect to={menuPath} />
      </Switch>
    </div>
  );
};

Restaurant.propTypes = {
  restaurant: PropTypes.shape({
    id: PropTypes.string.isRequired,
    name: PropTypes.string,
    menu: PropTypes.array,
    reviews: PropTypes.array,
  }).isRequired,
  averageRating: PropTypes.number,
};

const mapStateToProps = (state, props) => ({
  restaurant: restaurantSelector(state, props),
  averageRating: averageRatingSelector(state, props),
});

export default connect(mapStateToProps)(Restaurant);
