import { Redirect, Route, Switch } from 'react-router-dom';
import Restaurants from '../restaurants';
import Header from '../header';
import Basket from '../basket';
import OrderSuccess from '../order-success';
import OrderError from '../order-error';
import { UserProvider } from '../../contexts/user-context';
import { useState } from 'react';

const App = () => {
  const [name, setName] = useState('Andrey');
  const env = process.env.NODE_ENV;
  const domain = env === 'development' ? '' : '/react-2021-10-08';
  const homePage = `${domain}/`;

  debugger;
  return (
    <div>
      <UserProvider value={{ name, setName }}>
        <Header />
        <Switch>
          <Redirect exact from={homePage} to={`${domain}/restaurants`} />
          <Route path={`${domain}/checkout`} component={Basket} />
          <Route path={`${domain}/restaurants`} component={Restaurants} />
          <Route path={`${domain}/order-success`} component={OrderSuccess} />
          <Route path={`${domain}/order-error`} component={OrderError} />
          <Route
            path={`${domain}/error`}
            component={() => <h2>Error Page!</h2>}
          />
          <Route component={() => <h2>404 - Not found :(</h2>} />
        </Switch>
      </UserProvider>
    </div>
  );
};

export default App;
